.card{
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);

  text-align: center;
  font-family: Arial, Helvetica, sans-serif;

}
.price{
  color:black;
  font-size: 22px;
}
.card button{
 
  padding: 12px;
  outline: 0;
  color: white;
  background-color: #000;
  width: 100%;
  text-align: center;
  font-size: 22px;
  text-transform: uppercase;
}
.card button:hover{
  opacity:0.7 ;
  max-zoom: 100%;
}
h1{
  text-transform: uppercase;
}
.cards{
  margin: auto;
  max-width: 1000px;
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(225px,1fr));
grid-auto-rows: auto;
gap: 20px;
font-family: sans-serif;
padding-top: 30px;

}